<template>
  <div>
    <MHeader></MHeader>
    <div class="content" ref="scroll" @scroll="loadMore">
      <h3></h3>

      <ul>
         <router-link v-for="(book,index) in books" :to="{name:'datail',params:{id:book.bookId}}" :key="index" tag="li">

          <img v-lazy="book.bookCover" :alt="book.bookName">
          <div>
            <h4>{{book.bookName}}</h4>
            <p>{{book.bookInfo}}</p>
            <b>{{book.bookPrice}}</b>
            <button @click.stop="remove(book.bookId)"></button>
          </div>
         

        </router-link>
      </ul>
    
  </div>
  </div>
</template>
<script type="text/javascript">
import MHeader from "../base/MHeader.vue";
import {pagination,removeBook} from "../api";
  export default {
    name:"list",
    data(){
      return {books:[],offset:0,hasMore:true,isLoading:false}
    },
    mounted(){
      let scroll = this.$refs.scroll;//
      let top = scroll.offsetTop;//
      let distance = 0;//
      
      scroll.addEventListener("touchstart",(e)=>{
         if (scroll.scrollTop != 0 ) {return}
            console.log(scroll.scrollTop )
           //
          let start = e.touches[0].pageY;
          //<!------------------------
                    //
          let move = (e)=>{
          
             //
            let current = e.touches[0].pageY;
            //
            if (current - start > 0) {
              
              distance = current - start ;
             //
             if (distance <= 50) {
               scroll.style.top = distance+top+"px"  ;
             }else{
              distance = 50;
              scroll.style.top = top + 50 +"px"
             }
            
            }else{//moveend
              scroll.removeEventListener("touchmove",move);
              scroll.removeEventListener("touchend",end)

            }
          };
          //------------------------!>
         
        let end = (e)=>{
          clearTimeout(this.timer1);
          this.timer1 = setInterval(()=>{
            if (distance <= 0) {
              clearTimeout(this.timer1)
              distance = 0;
              scroll.style.top = 0;
            

              scroll.removeEventListener("touchmove",move);
              scroll.removeEventListener("touchend",end);
              this.books = [];//

              this.getData();
            }
            distance -= 1;
            scroll.style.top = distance + top +"px";
          },10)
         };

        scroll.addEventListener("touchmove",move);
        scroll.addEventListener("touchend",end);
        //
         

      })

    },

    components:{
      MHeader
    },
    methods:{
      loadMore(){
        let {scrollTop,clientHeight,scrollHeight}= this.$refs.scroll;
        // console.log(this.$refs.scroll.scrollTop)
        //<----------------------------
              //

        clearTimeout(this.timer),
        //timer vue
      this.timer = setTimeout(()=>{
           //
            if (scrollTop + clientHeight + 20 >= scrollHeight) {
            this.getData();
          }
        },20)

      //<-------------------------------
        
      },
       
        async getData(){

         let {data} =  await pagination(this.offset);
         let {hasMore,books} = data;
         // console.log(data)
         if (this.hasMore && !this.isLoading )  {
          this.isLoading = true;//
          this.books = [...this.books, ...books],
          this.hasMore = hasMore;
          this.isLoading = false;//
          // console.log(this.books)
          this.offset = this.books.length;//offset
         }

          

        }
     ,
      
      // reading(){
      //   getBook().then((res)=>{
      //     let {data} = res;
      //     this.books = data
      //   })
      // },
      remove(id){
        removeBook(id).then((res)=>{
          let {data} = res;
          this.books = this.books.filter(item => item.bookId !== id)

        })
      }


    },
    created(){
     // this.reading()
     this.getData()
    }

  }
</script>
<style type="text/css" scoped lang="scss">
h3{
  margin-top:40px;
  color:#999999;
  width:90%;
  margin:10px auto;

}
ul{
  width:90%;
  margin:0 auto;
  

  li{
     display:flex;
    // flex-wrap:wrap;
  border:1px solid #ccc;
    padding:10px 0 ;
    
    margin-bottom:10px;
    border-radius:25px;
    img{
      width:140px;
      height:150px;
    }
    div{
      // text-align:center;
      margin-left:10px;
      h4{
        font-size:20px;
        line-height:25px;
      }
      p{
        color:#229990;
        line-height:25px;
      }
      b{
        color:red;

      }
      button{
        display:block;
        width:60px;
        height:35px;
        background:orange;
        border:none;
        border-radius:15px;
        color:#ffffff;
        outline:none;

      }


      // text-align:center;
    }
  }

}
  
</style>


